.dark-mode-label{
  display: block;
}

@media screen and (min-width: 1024px) {
  .dark-mode-label{
    display: none;
  }
}

.DocSearch-Button {
  background: var(--color-white);
  background-position: 1em;
  background-repeat: no-repeat;
  border: 1px solid var(--color-platinum-gray);
  border-radius: 53px;
}

.DocSearch-Button-Container .DocSearch-Search-Icon {
  color: var(--docsearch-muted-color);
}

.navbar {
  background: var(--color-white);
}


.doc .sidebarblock.overview-box {
  border: 1px solid var(--color-silver-gray);
}

.navbar-brand .navbar-item:first-child {
  color: var(--color-black);
}

.starknet-logo {
  background-image: url(../img/starknet-logo-light.svg);
  width: 146px;
}

.toolbar {
  background-color: var(--color-smoke-80);
  border-top: 1px solid var(--divider-line-color);
}

.theme-toggle {
  background-image: url(../img/moon.svg);
  background-color: var(--color-white);
}

.theme-toggle:hover {
  /* background-image: url(../img/moon-filled.svg); */
  background-color: #f3f8fb;
}

.nav-toggle {
  background: url(../img/collapse-icon-outlined.svg) no-repeat 50% 47.5%;
}

/* .nav-toggle:not(.is-active):hover {
  background-image: url(../img/collapse-icon-filled.svg);
} */

.nav {
  border-right: 1px solid var(--divider-line-color);
}

.nav-panel-explore .component .version a {
  border: 1px solid var(--color-silver-gray);
  color: var(--color-blue-gray);
}

.nav-panel-explore .context:hover {
  color: var(--color-ultramarine-blue);
  background-color: var(--color-athens-white);
}

.doc .sidebarblock {
  background-color: var(--color-platinum-gray);
}

.doc .imageblock {
  border: var(--primary-border) 1px solid;
}

.doc .colist > table code,
.doc p code,
.doc thead code {
  background-color: var(--nav-selected-bg);
  color: var(--nav-selected-fc);
  font-weight: 400;
  font-size: 14px;
  font-family: "Fira Code";
  line-height: 14px;
}


.doc p a code,
.doc thead a code,
.doc .colist > table a code
{
  color: var(--color-light-accent-content);
}

.nav-list > .nav-item.is-current-path > span {
  color: var(--color-light-accent-content)!important;
}

pre {
  /* background-color: var(--color-platinum-gray); */
  padding: 0px;
}

footer.footer {
  border-top: 1px solid var(--divider-line-color);
}

.doc details {
  border: 1px solid var(--color-platinum-gray);
}

.doc table.tableblock {
  background-color: var(--color-white);
  box-shadow: 0 0 0 1px var(--color-silver-gray);
}

.doc table.tableblock,
.doc table.tableblock > * > tr > * {
  border-color: #EFEFEF;
}

.components-versions {
  border-right: 1px solid var(--divider-line-color);
}

#toc .sectlevel1 > li a {
  color: var(--color-ink-blue);
}

#toc .sectlevel1 > li a:visited {
  color: var(--color-ink-blue);
}

#toc {
  background-color: rgba(var(--rgb-silver-gray), 0.2);
  color: var(--color-ink-blue);
}

.doc details > summary::after {
  left: 0;
  background-image: url(../img/chevron-down.svg);
}

.doc details[open] > summary::after {
  background-image: url(../img/chevron-up-black.svg);
}

.tablist li.is-selected::before {
  content: "";
  position: absolute;
  bottom: -9px; /* adjust the value as needed */
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-light-accent-content);
  z-index: 1;
}

.tablist li {
  color: var(--color-light-accent-tertiary);
}

.tablist li.is-selected {
  color: var(--color-light-accent-secondary);
}
